import React from 'react';
import styles from './UpdateUserMsgForm.css';
import {Form, Input, Button} from 'antd';

const FormItem = Form.Item;

class UpdateUserMsgForm extends React.Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.initRequest = this.initRequest.bind(this);
  }

  componentDidMount() {
    this.props.dispatch({
      type: 'modelIndexPage/loadUserMessage',
      payload: null,
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.initRequest(values);
      }
    });
  };

  initRequest(values) {
    this.props.dispatch({
      type: 'modelIndexPage/submitUpdateUserMsgData',
      payload: values,
    })
  }

  render() {
    const data = this.props.userMsg.data;
    console.log(data);
    const {getFieldDecorator} = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 4},
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 20},
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 20,
          offset: 2,
        },
        sm: {
          span: 20,
          offset: 2,
        },
      },
    };
    return (
      <Form onSubmit={this.handleSubmit} style={{width: '100%', height: '100%'}}>
        <FormItem
          label={'真实姓名'}
          hasFeedback
          {...formItemLayout}
          className={styles.formItem}
        >
          {getFieldDecorator('username', {
            rules: [{required: true, message: '请输入真实姓名,不能为空', whitespace: true}],
            initialValue: data.realName,
          })(
            <Input/>
          )}
        </FormItem><FormItem
        label={'性别'}
        hasFeedback
        {...formItemLayout}
        className={styles.formItem}
      >
        {getFieldDecorator('sex', {
          rules: [{whitespace: true}],
          initialValue: (data.sex === 0) ? '男' : (data.sex === 1 ? '女' : ''),
        })(
          <Input/>
        )}
      </FormItem><FormItem
        label={'办公电话'}
        hasFeedback
        {...formItemLayout}
        className={styles.formItem}
      >
        {getFieldDecorator('officePhone', {
          rules: [{whitespace: true}],
          initialValue: '',
        })(
          <Input/>
        )}
      </FormItem><FormItem
        label={'移动电话'}
        hasFeedback
        {...formItemLayout}
        className={styles.formItem}
      >
        {getFieldDecorator('telephone', {
          rules: [{required: true, message: '请输入移动电话,不能为空', whitespace: true}],
          initialValue: data.mobilePhone,
        })(
          <Input/>
        )}
      </FormItem><FormItem
        label={'email'}
        hasFeedback
        {...formItemLayout}
        className={styles.formItem}
      >
        {getFieldDecorator('email', {
          rules: [{required: true, message: '请输入email,不能为空', whitespace: true}],
          initialValue: data.email,
        })(
          <Input/>
        )}
      </FormItem><FormItem
        label={'部门名称'}
        hasFeedback
        {...formItemLayout}
        className={styles.formItem}
      >
        {getFieldDecorator('departmentName', {
          rules: [{whitespace: true}],
          initialValue: data.orgName,
        })(
          <Input/>
        )}
      </FormItem>
        <div className={styles.buttonContainer}>
          <FormItem
            {...tailFormItemLayout}
            className={styles.formItemTwo}
          >
            <Button type="primary" htmlType="submit" className={styles.button}>确定</Button>
          </FormItem><FormItem
          {...tailFormItemLayout}
          className={styles.formItemTwo}
        >
          <Button type="default" className={styles.button}>修改密码</Button>
        </FormItem>
        </div>
      </Form>
    );
  }
}

const WrappedUpdateUserMsgForm = Form.create()(UpdateUserMsgForm);

export default WrappedUpdateUserMsgForm;
